/* Evita overflow horizontal */
html, body {
  overflow-x: hidden;
}

/* Reset básico para remover margens/paddings padrão */
* {
  padding: 0;
  box-sizing: border-box;
}

body {
  margin: 0;
  padding: 0;
}

/* Fonte local para o Navbar */
@font-face {
  font-family: 'MinhaFonte';
  src: url(/conviteCasamento/static/media/FontsFree-Net-lawrence-regular.8fcaf569.woff2) format('woff2');
  font-weight: 500;
  font-style: normal;
}

/* Barra de navegação principal */
.navbar {
  position: fixed;
  top: 0;
  left: 0;
  width: 100vw; /* Usa 100vw para garantir que não ultrapasse a viewport */
  z-index: 999;
  
  display: flex;
  align-items: center;
  justify-content: space-between;
  
  padding: 0.5rem 1rem;
  background-color: #fff;
  box-shadow: 0 20px 20px rgba(0, 0, 0, 0.1);
  font-family: 'MinhaFonte', sans-serif;
}

/* Logo */
.nav-logo {
  font-size: 2rem;
  color: #333;
  white-space: nowrap;
}

/* Data (oculta por padrão) */
.nav-date {
  display: none;
}

/* Lista de links */
.nav-links {
  list-style: none;
  display: flex;
  align-items: center;
  grid-gap: 2rem;
  gap: 2rem;
}

/* Links */
.nav-links a {
  text-decoration: none;
  color: #333;
  font-weight: 600;
  font-size: 1.2rem;
  transition: color 0.3s ease;
}

.nav-links a:hover {
  color: #666;
}

/* Botão de confirmação de presença */
.confirm-button {
  padding: 0.5rem 1rem;
  border: 2px solid #333;
  border-radius: 4px;
  background-color: #fff;
  color: #333;
  font-size: 1.2rem;
  transition: background-color 0.3s ease, color 0.3s ease;
}

.confirm-button:hover {
  background-color: #333;
  color: #fff;
}

/* Ícone de menu hamburguer - escondido em telas grandes */
.mobile-menu-icon {
  display: none;
  flex-direction: column;
  grid-gap: 5px;
  gap: 5px;
  cursor: pointer;
}

.mobile-menu-icon .bar {
  width: 25px;
  height: 3px;
  background-color: #333;
}

/* Media Query para telas menores */
@media (max-width: 768px) {
  /* Reduz o padding da navbar para caber melhor na tela */
  .navbar {
    padding: 0.5rem;
    width: 100vw; /* Garante que a navbar não extrapola a largura da viewport */
  }
  
  /* Reduz o tamanho da logo */
  .nav-logo {
    font-size: 1.8rem;
  }
  
  /* Exibe o ícone do menu hamburguer */
  .mobile-menu-icon {
    display: flex;
  }
  
  /* Exibe a data centralizada vertical e horizontalmente */
  .nav-date {
    display: block;
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    font-size: 1rem;
    color: #333;
  }
  
  /* Ajusta os links para ficarem menores e com menos espaçamento */
  .nav-links {
    position: fixed;
    top: 60px; /* Espaço suficiente para a navbar */
    left: 0;
    width: 100vw; /* Usa 100vw para evitar overflow horizontal */
    background-color: #fff;
    flex-direction: column;
    align-items: center;
    grid-gap: 0.5rem;
    gap: 0.5rem;
    padding: 0.5rem 0;
    display: none;
    z-index: 998;
  }
  
  /* Exibe os links quando o menu estiver aberto */
  .nav-links.mobile-menu-open {
    display: flex;
  }
  
  /* Reduz o tamanho dos links e do botão */
  .nav-links a,
  .confirm-button {
    font-size: 1rem;
  }

  /* Adiciona espaçamento superior ao conteúdo principal quando o menu está aberto */
  body.mobile-menu-open {
    padding-top: 220px; /* Ajuste dependendo da altura da navbar quando aberta */
  }
}

