/* Container do header */
.header {
  /* Atualiza a cor de fundo para #FFFCF8 */
  background-color: #FFFCF8;

  /* Imagem de fundo */
  background-image: url(/conviteCasamento/static/media/background.6fc22055.jpg);
  background-repeat: no-repeat;
  background-size: cover; /* Para preencher todo o espaço (corta um pouco se necessário) */
  background-position: top center; /* Imagem começa no topo e centralizada horizontalmente */

  /* Aumenta o height do header alterando a min-height para 120vh (ajuste conforme desejado) */
  min-height: 70vh;

  /* Remove padding-top para que a imagem inicie no topo */
  padding-top: 0;

  /* Centraliza qualquer conteúdo direto no .header */
  text-align: center;
  position: relative;
}

.hero {
  padding-top: 250px; 

  font-family: 'Lawrence', sans-serif;
  color: #274D66;
  max-width: 800px;
  margin: 0 auto;
  text-align: center;
}

/* Título (Felipe & Esther) */
.hero h1 {
  font-size: 5.5rem;
  margin: 0;
  margin-bottom: 0.5rem;
  color: #344B68;
}

/* Subtítulo (data, local, etc.) */
.hero p {
  margin: 0;
  margin-top: 0.5rem;
  font-size: 1.7rem;
}

/* ---------------- Responsive ---------------- */

/* Para telas com largura até 1024px */
@media (max-width: 1024px) {
  .hero {
      padding-top: 180px; /* Reduz o padding superior */
      max-width: 90%;
  }

  .hero h1 {
      font-size: 5rem;
  }

  .hero p {
      font-size: 1.6rem;
  }

  /* Ajusta o posicionamento da imagem para telas menores */
  .header {
      background-size: contain; /* Ajuste a imagem para se ajustar dentro da tela */
      background-position: top center; /* A imagem vai se alinhar mais para o topo */
  }
}

/* Para telas com largura até 768px */
@media (max-width: 768px) {
  .hero {
      padding-top: 120px; /* Ajuste o padding superior para começar mais próximo da navbar */
      max-width: 90%;
  }

  .hero h1 {
      font-size: 4.5rem; /* Tamanho menor para o título */
  }

  .hero p {
      font-size: 1.4rem; /* Tamanho menor para o subtítulo */
  }

  /* Ajuste o padding-top para começar abaixo da navbar */
  .header {
      padding-top: 0; /* Remover espaço excessivo */
      background-size: contain; /* Ajuste a imagem para se ajustar dentro da tela */
      background-position: top center; /* Coloca a imagem mais para cima */
  }
}

/* Para telas com largura até 480px */
@media (max-width: 480px) {
  .hero {
      padding-top: 80px; /* Ajuste o valor para não causar excesso de espaço */
      max-width: 95%;
  }

  .hero h1 {
      font-size: 3.5rem; /* Título ajustado para dispositivos muito pequenos */
  }

  .hero p {
      font-size: 1.2rem; /* Subtítulo ajustado para melhor visualização */
  }

  /* Ajusta o padding-top para começar abaixo da navbar */
  .header {
      padding-top: 0; /* Remover o espaço extra abaixo da navbar */
      background-size: contain; /* Ajuste a imagem para se ajustar dentro da tela */
      background-position: top center; /* A imagem começa mais para cima */
  }
}

/* Estilo para o componente de contagem regressiva */
.countdown {
  margin-top: 10rem;      /* Espaçamento superior opcional */
  color: #274D66;         /* Cor principal do texto */
}

/* Título da contagem */
.countdown-title {
  font-size: 1.5rem;
  margin-bottom: 1rem;
}

/* Container que agrupa cada item (dias, horas, minutos) */
.countdown-items {
  display: flex;
  justify-content: center; /* Centraliza horizontalmente */
  grid-gap: 3rem;
  gap: 3rem;              /* Espaço entre cada bloco */
}

/* Cada bloco individual (ex.: "40" + "dias") */
.countdown-item {
  display: flex;
  flex-direction: column; /* Número em cima, texto embaixo */
  align-items: center;
}

/* O número em destaque */
.number {
  font-size: 3rem;
  color: #274D66;
}

/* O rótulo (dias, horas, minutos) */
.label {
  font-size: 1.5rem;
  color: #555;
}

/* Media Query para telas menores */
@media (max-width: 768px) {
  .countdown {
    margin-top: 5rem;  /* Reduz o espaço superior em telas menores */
  }

  /* Ajusta o tamanho do título */
  .countdown-title {
    font-size: 1.2rem;
  }

  /* Mantém os itens em linha mesmo em telas menores */
  .countdown-items {
    grid-gap: 2rem;
    gap: 2rem; /* Reduz o espaço entre os itens */
    flex-wrap: nowrap; /* Garante que os itens fiquem lado a lado */
  }

  .countdown-item {
    flex-direction: column; /* Organiza os itens em coluna (número em cima, rótulo embaixo) */
  }

  /* Ajusta o tamanho das fontes */
  .number {
    font-size: 2.5rem;  /* Reduz o tamanho do número */
  }

  .label {
    font-size: 1.2rem;  /* Reduz o tamanho do rótulo */
  }
}

/* Media Query para telas extra pequenas */
@media (max-width: 480px) {
  .countdown {
    margin-top: 3rem;  /* Ajusta o espaço superior para telas muito pequenas */
  }

  /* Ajusta o tamanho do título */
  .countdown-title {
    font-size: 1rem;
  }

  /* Mantém os itens em linha para telas extra pequenas */
  .countdown-items {
    grid-gap: 1.5rem;
    gap: 1.5rem;               /* Reduz o espaço entre os itens */
    flex-wrap: nowrap;          /* Garante que os itens fiquem lado a lado */
  }

  .countdown-item {
    flex-direction: column; /* Mantém a disposição em coluna */
  }

  /* Ajuste de tamanho para os números */
  .number {
    font-size: 2rem;  /* Reduz ainda mais o tamanho do número */
  }

  /* Ajuste de tamanho para os rótulos */
  .label {
    font-size: 1rem;  /* Reduz ainda mais o tamanho do rótulo */
  }
}

/* Seção de contagem regressiva */
.countdown-section {
  width: 100%;
  background-color: #FFFCF8;  /* Fundo desejado */
  text-align: center;
  padding: 3rem 1rem;         /* Espaçamento interno */
  color: #3F6F90;             /* Cor principal do texto */
  box-sizing: border-box;
  font-family: 'Lawrence', sans-serif;  /* Aplica a fonte importada */
}

/* Título da seção */
.countdown-section h2 {
  font-size: 2.5rem;
  margin-bottom: 1rem;
}

/* Parágrafos da seção */
.countdown-section p {
  max-width: 900px;           /* Limita a largura do texto */
  margin: 0 auto 1rem;
  line-height: 1.5;
  font-size: 1.5rem;
  color: black;
}

/* Estilo da assinatura */
.signature {
  font-style: italic;
  margin-bottom: 2rem;
}

/* Estilo para o componente de contagem (se o CountdownTimer utiliza a classe .countdown) */
.countdown {
  font-size: 2rem;
  margin-top: 1rem;
  color: #555;
}

/* Media Query para telas menores */
@media (max-width: 768px) {
  .countdown-section {
    padding: 1.5rem 1rem; /* Reduz o padding superior em telas pequenas */
  }

  /* Ajusta o tamanho do título para telas menores */
  .countdown-section h2 {
    font-size: 2rem;  /* Reduz o tamanho do título */
  }

  /* Ajusta o tamanho do texto nos parágrafos para telas menores */
  .countdown-section p {
    font-size: 1.2rem;  /* Reduz o tamanho da fonte */
  }

  /* Ajusta o tamanho da contagem regressiva para telas menores */
  .countdown {
    font-size: 1.5rem;  /* Reduz o tamanho da fonte da contagem regressiva */
  }
}

/* Media Query para telas extra pequenas */
@media (max-width: 480px) {
  .countdown-section {
    padding: 1rem 1rem;  /* Reduz o padding ainda mais para telas muito pequenas */
  }

  .countdown-section h2 {
    font-size: 1.8rem;  /* Ajusta o título para telas ainda menores */
  }

  .countdown-section p {
    font-size: 1rem;  /* Ajuste de tamanho de fonte nos parágrafos */
  }

  .countdown {
    font-size: 1.2rem;  /* Ajusta o tamanho da contagem regressiva */
  }
}

